<template>
    <div class="container text-center">
        <div class="row">
            <div class="col-3">
                <div class="Head001">用户信息</div>
                <div>
                    <div class="card cb">
                        <div class="card-body">
                            <div class="form-floating">
                            <textarea v-model="intext" class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" style="height: 100px"></textarea>
                            <label for="floatingTextarea2">分享现在</label>
                            </div>
                            <button type="button" @click="postnew" class="btn btn-primary btn-sm btn-sub">发帖</button>
                            <!-- <p>{{ intext }}</p> -->
                        </div>
                    </div>
                </div>
            </div>
            <div class="col-9">
                <div class="Head001">用户动态</div>
                <div v-for="item in Post.body" :key="item.id">
                    <div class="card cb">
                        <div class="card-body">
                            {{ item.text }}
                        </div>
                    </div>
                </div>
            </div>
        </div> 
        
        
        
    </div>
</template>

<script>
import { ref } from 'vue';


    export default {
        name: 'listPlay',
        components: {

        },  
        props: {
            Post: {
                type: Object,
                default: () => {},
            }
            
        },
        setup: (props, father)=> {
            let intext = ref('');
            const postnew = ()=> {
                father.emit('postnew', intext.value);
                intext.value = '';
            };
            return {
                intext,
                postnew,
            }
        },  
    }
</script>

<style scoped>
.Head001 {
    font-size: small;
    color: grey;
}
.cb {
    margin: 15px 0px;
}
.btn-sub {
    margin: 10px;
    padding: 3px 15px;
}
</style>