<template>
    <container>
        <listPlay :Post="Post" @postnew="postnew"></listPlay>
    </container>
</template>

<script>
import container from '../components/container.vue'
import listPlay from '@/components/list_play.vue';
import { reactive } from 'vue';

    export default {
        name: 'friendsList',
        components: {
            container,
            listPlay,
        },
        
        setup: ()=> {
            const Post = reactive({
                Count: 2,
                body: [
                    {
                        id: 1,
                        text: 'aaaaaa',
                    },
                    {
                        id: 2,
                        text: 'bbbbb',
                    },
                ],
            });
            const postnew = (context)=> {
                Post.Count ++;
                Post.body.unshift({
                    id: Post.Count,
                    text: context,
                });
            };
            return {
                Post,
                postnew,
            }
        },

    }
</script>

