<template>
    <container>
        <div class="row text-center">
            <div class="col-3">
                <div class="Head001">用户信息</div>
                <idcard :userid="NowId" style="margin-top: 10px"> </idcard>
                <sendsmg @newpost="newpost"></sendsmg>
            </div>
            <div class="col-9">
                <div class="Head001">用户动态</div>
                <newsList :Post="Post"></newsList>
            </div>
        </div>
    </container>
</template>

<script>
import container from '@/components/container.vue';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
import router from '@/router';
import idcard from '@/components/idcard.vue';
import sendsmg from '@/components/sendsmg.vue';
import newsList from '@/components/newsList.vue';
import { reactive } from 'vue';
import $ from 'jquery';

    export default {
        name: 'userProf',
        components: {
            container,
            idcard,
            sendsmg,
            newsList
        },
        setup: ()=> {
            const route = useRoute();
            const store = useStore();
            let NowId = route.params.userId;
            if (NowId == 0) {
                NowId = store.state.User.userId;
            }
            const access = store.state.User.access;
            let Post = reactive({
                Count: 0,
                posts: [],
            });
            console.log(NowId);
            if (!store.state.User.isLogin) {
                router.push({
                    name: 'login',
                });
            }
            else {
                $.ajax({
                    type: "get",
                    url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                    data: {
                        user_id: NowId,
                    },
                    headers: {
                        'Authorization': 'Bearer ' + access,
                    },
                    success: function (response) {
                        console.log(response);
                        Post.posts = response;
                    }
                });
            };
            const newpost = (content)=> {
                $.ajax({
                    type: "POST",
                    url: "https://app165.acapp.acwing.com.cn/myspace/post/",
                    data: {
                        content: content,
                    },
                    headers: {
                        'Authorization': 'Bearer ' + access,
                    },
                    success: function (response) {
                        console.log(response);
                    }
                });
                Post.Count ++;
                Post.posts.unshift({
                    id: Post.Count,
                    content: content,
                });
            }
            return {
                NowId,
                Post,
                newpost,
            }
        }
    }
</script>

<style scoped>
.Head001 {
    font-size: small;
    color: grey;
}
</style>