import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import No_find from '../views/find_404.vue' 
import friendsList from '../views/friendsList.vue'
import login from '../views/login.vue'
import userProf from '../views/userProf.vue'
const routes = [
  {
    path: '/index.html',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/index.html/userProf/:userId',
    name: 'userProf',
    component: userProf,
  },
  {
    path: '/index.html/friendsList',
    name: 'friendsList',
    component: friendsList,
  },
  {
    path: '/index.html/login',
    name: 'login',
    component: login,
  },
  {
    path: '/index.html/404',
    name: 'notfind',
    component: No_find,
  },
  {
    path: '/index.html/:path(.*)',
    redirect: '/index.html/404',
  }
  
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
