<template>
    <container>
        <div class="row justify-content-md-center">
            <div class="col-3">
                <div class="form-floating mb-3">
                <input v-model="username" type="text" class="form-control" id="floatingInput" placeholder="用户名">
                <label for="floatingInput">用户名</label>
                </div>
                <div class="form-floating">
                <input v-model="password" type="password" class="form-control" id="floatingPassword" placeholder="密码">
                <label for="floatingPassword">密码</label>
                </div> 
                <div v-if="loginfail">
                  <span>用户名或者密码错误</span>  
                </div>
                <button @click="login" type="button" class="btn btn-primary btn-login">登录</button>
            </div>
            
        </div>
        
    </container>
</template>

<script>
import container from '@/components/container.vue';
import { ref } from 'vue';
import router from '@/router';
import { useStore } from 'vuex';

    export default {
        name: 'login',
        components: {
            container,
        },
        setup: ()=> {
            let username = ref('');
            let password = ref('');
            let loginfail = ref(false);
            const store = useStore();

            const login = ()=> {
                const logpassed = password.value;
                password.value = '';
                store.dispatch('login', {
                    username: username.value,
                    password: logpassed,
                    success: () => {
                        console.log('success');
                        loginfail.value = false;
                        router.back();
                    },
                    error: () => {
                        console.log('error');
                        loginfail.value = true;
                    }
                });
                
            };
            return {
                username,
                password,
                login,
                loginfail,
            }
        }, 
    }
</script>

<style scoped>
.btn-login {
    width: 100%;
    margin-top: 10px;
}
span {
    color: red;
}
</style>