import $, { data } from 'jquery'
import jwtDecode from "jwt-decode";

const UseModule = ({
    state: {
        username: '',
        userId: 0,
        followsCount: 0,
        refresh: '',
        access: '',
        isLogin: false,
        photo: '',
    },
    mutations: {
        update: (state, data) => {
            state.userId = data.userId;
            state.isLogin = data.isLogin;
            state.followsCount = data.followsCount;
            state.refresh = data.refresh;
            state.access = data.access;
            state.photo = data.photo;
            state.username = data.username;
            console.log('ok!!!');
        },
        updateAccess: (state, data) => {
            state.access = data;
            console.log(data);
        },
        logout: (state) => {
            state.userId = 0;
            state.isLogin = false;
            state.followsCount = 0;
            state.refresh = '';
            state.access = '';
            state.photo = '';
            state.username = '';
        }

    },
    actions: {
        login: (context, user)=> {
            $.ajax({
                type: "post",
                url: "https://app165.acapp.acwing.com.cn/api/token/",
                data: {
                    username: user.username,
                    password: user.password,
                },
                success: function (response) {
                    const {access, refresh} = response;
                    
                    const info = jwtDecode(access);
                    context.dispatch('GetUserInfo', {
                        user_id: info.user_id,
                        access: access,
                        success: (resp) => {
                            context.commit('update', {
                                username: resp.username,
                                userId: info.user_id,
                                followsCount: resp.followerCount,
                                refresh: refresh,
                                access: access,
                                isLogin: true,
                                photo: resp.photo,
                            });
                            var sw = setInterval(() => {
                                if (context.state.isLogin)
                                    context.dispatch('Refresh');
                                else {
                                    clearInterval(sw);
                                }
                            }, 4.5 * 60 * 1000);
                            user.success();
                        },
                        error: ()=> {
                            user.error();
                        }
                    });
                },
                error: function () {
                    user.error();
                }
            });
        },
        Refresh: (context)=> {
            const refreshdata = context.state.refresh;
            $.ajax({
                type: "post",
                url: "https://app165.acapp.acwing.com.cn/api/token/refresh/",
                data: {
                    refresh: refreshdata,
                },
                success: function (response) {
                    context.commit('updateAccess', response.access);
                }
            });
        },
        GetUserInfo: (context, user_data) => {
            $.ajax({
                type: "get",
                url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
                data: {
                    user_id: user_data.user_id,
                },
                headers: {
                    'Authorization': 'Bearer ' + user_data.access,
                },
                success: function (rep) {
                    user_data.success(rep);
                },
                error: function () {
                    user_data.error();
                }
            })
        },
    },
});

export default UseModule;
