<template>
    <div class="form-floating cb">
    <textarea v-model="intext" class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" style="height: 100px"></textarea>
    <label for="floatingTextarea2">分享现在</label>
    </div>
    <button type="button" class="btn btn-primary btn-sm btn-sub" @click="newpost">发帖</button>
</template>

<script>
import { ref } from 'vue';

    export default {
        name: 'sendsmg',
        setup: (props, context)=> {
            let intext = ref('');
            const newpost = () => {
                if (intext.value != '') {
                    context.emit('newpost', intext.value);
                    intext.value = '';
                }
            };
            return {
                intext,
                newpost,
            }
        },  
    }
</script>

<style scoped>
.cb {
    margin-top: 15px;
}
.btn-sub {
    margin: 10px;
    padding: 3px 15px;
    width: 30%;
}
</style>