<template>
    <container>
        
        <div v-for="item in info" :key="item.id" class="card">
            <div class="card-body">
               <img :src="item.photo" alt="" srcset="">
                {{ item.username }}
            </div>
        </div>
    </container>
    
</template>

<script>
import container from '../components/container.vue';
import $ from 'jquery';
import { ref } from 'vue';

    export default {
        name: 'HomeView',
        components: {
            container,
        },
        setup: ()=> {
            let info = ref([]);
            $.ajax({
                type: "get",
                url: "https://app165.acapp.acwing.com.cn/myspace/userlist/",
                dataType: "Json",
                success: function (response) {
                    console.log(response);
                    info.value = response;
                }
            });
            // console.log()
            return {
                info,
            }
        },  
    }
</script>

