<template>
    <div class="card">
        <div class="card-body">
            <div class="row">
                <div class="col-5">
                    <img :src="photourl" alt="" class="img-fluid rop">
                </div>
                <div class="col-7">
                    <p class="Name">{{ name }}</p>
                    <p class="follow">粉丝： {{ followerCount }}</p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import store from '@/store';
import $ from 'jquery'
import { ref } from 'vue';
    export default {
        name: 'idcard',
        props: {
            userid: {
                type: Number,
                
            }
        },
        setup: (props, context)=> {
            const access = store.state.User.access;
            console.log(props);
            let name = ref('');
            let photourl = ref('');
            let followerCount = ref(0);
            $.ajax({
                
                type: "get",
                url: "https://app165.acapp.acwing.com.cn/myspace/getinfo/",
                data: {
                    user_id: props.userid,
                },
                headers: {
                    'Authorization': 'Bearer ' + access,
                },
                success: function (response) {
                    name.value = response.username;
                    photourl.value = response.photo;
                    followerCount.value = response.followerCount;
                }
            });
            return {
                name,
                photourl,
                followerCount,
            }
        }
    }

</script>

<style scoped>
.rop {
    border-radius: 50%;
}
.Name {
    font-size: 20px;
    font-weight: bold;
}
.follow {
    font-size: 15px;
    color: gray;
}
</style>